import React, { useContext } from 'react';
import DeviceContext from '../../DeviceContext';
import { Button, withStyles } from '@material-ui/core';
import FileReaderInput from 'react-file-reader-input';

const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

const DeviceActions = ({classes}) => {
  const context = useContext(DeviceContext);

  if (!context.device) {
    return <Button variant="contained" color="primary" onClick={context.connect}>Connect Printer</Button>
  } 

  const selectGCodeFile = (e, results) => {
    results.forEach(result => {
      const [e, file] = result;
      console.log(`Processing ${file.name}`)
      context.sendGCode(e.target.result);
    });
  }

  return (
    <React.Fragment>
      <FileReaderInput as="binary" id="my-file-input" tabIndex="-1" onChange={selectGCodeFile}>
        <Button variant="contained" color="primary" className={classes.button}>Print GCode File</Button>
      </FileReaderInput>
      <Button variant="outlined" onClick={context.loadFilament} className={classes.button}>Load Filament</Button>
      <Button variant="outlined" onClick={context.unloadFilament} className={classes.button}>Unload Filament</Button>
      <Button onClick={context.disconnect} className={classes.button}>Disconnect Printer</Button>
    </React.Fragment>
  );
};

export default withStyles(styles)(DeviceActions);
