import React, { useState } from 'react';
import { Info } from '@material-ui/icons';
import { IconButton, Dialog, DialogContent, DialogTitle } from '@material-ui/core';

const STATUS_DISCONNECTED = 'disconnected';

function getStatus(status) {
  if (status && status.status && status.status.state) {
    return status.status.state;
  } else {
    return "Disconnected";
  }
}

export default ({status}) => {
  const [modalOpen, setModalOpen] = useState(false);
  
  return (
    <span>
      {`Printer Status: ${getStatus(status)}`}
      <IconButton aria-label="Status Details" onClick={() => setModalOpen(true)}>
        <Info />
      </IconButton>
      <Dialog open={modalOpen} onClose={() => setModalOpen(false)}>
        <DialogTitle>Status Details</DialogTitle>
        <DialogContent style={{whiteSpace: 'pre-wrap'}}>
          {JSON.stringify(status, null, 2)}
        </DialogContent>
      </Dialog>
    </span>
  );
};