import React, { useContext } from 'react';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import FileReaderInput from 'react-file-reader-input';

import Banner from '../../Banner';
import DeviceContext from '../../DeviceContext';
import Status from '../../cards/print/Status';

export default function MainPage() {
  const context = useContext(DeviceContext);

  const selectGCodeFile = (e, results) => {
    results.forEach(result => {
      const [e, file] = result;
      console.log(`Processing ${file.name}`)
      context.sendGCode(e.target.result);
    });
  }

  return (
    <Card>
      <CardContent>
        <Banner />
      </CardContent>
      <CardActions>
        <FileReaderInput as="binary" id="my-file-input" tabIndex="-1" onChange={selectGCodeFile}>
          <Button variant="contained" color="primary">Print GCode File</Button>
        </FileReaderInput>
        <Button variant="outlined" onClick={context.loadFilament}>Load Filament</Button>
        <Button variant="outlined" onClick={context.unloadFilament}>Unload Filament</Button>
        <Button onClick={context.disconnect}>Disconnect</Button>
      </CardActions>
      <CardContent>
        <Status status={context.status} />
      </CardContent>
    </Card>
  );
}